<template>
  <div class="small">
    <line-chart
      :chart-data="datacollection"
      :options="options"
      class="chart"
      :styles="myStyles"
    ></line-chart>
  </div>
</template>

<script>
import LineChart from '@/plugins/LineChart'

export default {
  components: {
    LineChart
  },
  data () {
    return {
      render: 0,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [{
            gridLines: {
              display: false
            },
            scaleLabel: {
              display: true,
              labelString: 'Time, hours'
            }
          }],
          yAxes: [{
            gridLines: {
              color: 'rgb(226, 226, 226)'
            },
            scaleLabel: {
              display: true,
              labelString: 'Radius, nm'
            }
          }]
        }
      },
      datacollection: {}
    }
  },
  computed: {
    myStyles () {
      return {
        height: '100%',
        width: '100%'
      }
    }
  },
  mounted () {
    this.$store.watch(() => this.$store.getters.calculation,
      () => {
        this.datacollection = this.fillData(this.$store.getters.calculation)
      }, { deep: true })
  },
  methods: {
    fillData (value) {
      const lines = {
        labels: value.time,
        datasets: []
      }
      for (const carbide of value.dataset) {
        const colorEgle = Math.floor(Math.random() * 360)
        lines.datasets.push({
          label: carbide.element,
          backgroundColor: `hsla(${colorEgle}, 100%, 70%, .5)`,
          borderColor: `hsl(${colorEgle}, 100%, 40%)`,
          data: carbide.radius
        })
      }
      return lines
    }
  }
}
</script>

<style lang="scss" scoped>
  .small {
    height: 50vh;
    margin-left: 30px;
    .chart{
      position: relative;
    }
  }
</style>
