<template>
  <div class="data__content">
    <div class="title">{{ db.grade }}</div>
    <ul>
        <li>
          <select
            name="db"
            v-model="currentDb"
            class="select"
          >
            <option
              v-for="entry of Object.keys(db)"
              :value="entry"
              :key="entry"
              class="option"
            >
            {{ entry }} [ {{ db[entry].code }} ]</option>
          </select>
        </li>
    </ul>
  </div>
</template>
<script>
export default {
  data: () => ({
    currentDb: '1',
    db: {}
  }),
  async mounted () {
    this.db = this.$store.getters.db[0]
    this.$store.dispatch('setDb', this.db[this.currentDb])
  },
  watch: {
    currentDb () {
      this.$store.dispatch('setDb', this.db[this.currentDb])
    }
  },
  filters: {
    prettyComposition: function (value) {
      let res = ''
      if (!value) return 'Nothing here'
      Object.keys(value).forEach(el => {
        res += `${el}: ${(value[el]).toFixed(2)}% `
      })
      return res
    }
  }
}
</script>
