.option {
  font-size: inherit;
  width: 100%;
  padding: 6px 0;
  margin: 3px 0;
  background: rgb(231, 127, 7);
  box-shadow: 1px 1px 3px rgba($color: #000000, $alpha: .7);
}
.sample__composition {
  max-width: 150px;
}
.title {
  text-align: center;
  font-size: 14px;
  font-weight: bold;
}
</style>
