<template>
  <div class=strenght>
    <div class="data__wrapper">
      <strength-data />
      <strenght-controls />
    </div>
    <div class="chart__wrapper">
      <chart-db />
      <chart-strenth />
    </div>
  </div>
</template>

<script>
import StrengthData from '@/components/StrengthData'
import StrenghtControls from '@/components/StrengthControls'
import ChartDb from '@/components/ChartDb'
import ChartStrenth from '@/components/ChartStrenth'
import strengthCalc from '@/plugins/StrengthCalc.js'

export default {
  components: {
    StrengthData,
    StrenghtControls,
    ChartDb,
    ChartStrenth
  },
  data () {
    return {
      applyVariables: null
    }
  },
  created () {
    this.$store.watch(() => this.$store.getters.current,
      async () => {
        this.applyVariables = await strengthCalc(this.$store.getters.current)
        this.$store.dispatch('setCalculation', this.applyVariables(this.$store.getters.variables))
      }, { deep: true })
    this.$store.watch(() => this.$store.getters.variables,
      async () => {
        if (this.applyVariables) {
          this.$store.dispatch('setCalculation', await this.applyVariables(this.$store.getters.variables))
        }
      }, { deep: true })
  }
}
</script>
