export default {
  state: {
    db: {},
    diffusonDb: {},
    otherData: {},
    current: {
      db: {}
    },
    variables: {},
    calculation: {
      time: [],
      dataset: [{
        radius: [],
        element: ''
      }]
    }
  },
  mutations: {
    setData (state, { db, diffusonDb, otherData }) {
      state.db = db
      state.diffusonDb = diffusonDb
      state.otherData = otherData
    },
    setDb (state, value) {
      state.current.db = value
    },
    setVariables (state, value) {
      state.variables = value
    },
    setCalculation (state, value) {
      state.calculation = value
    }
  },
  actions: {
    clear ({ dispathc, commit }) {
      localStorage.clear()
    },
    async fetchData ({ dispathc, commit }) {
      try {
        if (!localStorage.getItem('db')) {
          const dbFile = await import('@/resources/db.json') || '{}'
          localStorage.setItem('db', JSON.stringify(dbFile))
        }
        if (!localStorage.getItem('diffusonDb')) {
          const diffusionDb = await import('@/resources/diffusionData.json') || '{}'
          localStorage.setItem('diffusionDb', JSON.stringify(diffusionDb))
        }
        if (!localStorage.getItem('otherData')) {
          const otherData = await import('@/resources/otherData.json') || '{}'
          localStorage.setItem('otherData', JSON.stringify(otherData))
        }
        commit('setData', {
          db: JSON.parse(localStorage.getItem('db')),
          diffusionDb: JSON.parse(localStorage.getItem('diffusionDb')),
          otherData: JSON.parse(localStorage.getItem('otherData'))
        })
      } catch (e) {}
    },
    async setDb ({ dispathc, commit }, value) {
      commit('setDb', value)
    },
    async setCalculation ({ dispathc, commit }, value) {
      await commit('setCalculation', value)
    },
    async setVariables ({ dispathc, commit }, value) {
      commit('setVariables', value)
    }
  },
  getters: {
    db: s => s.db,
    current: state => state.current,
    calculation: state => state.calculation,
    variables: state => state.variables
  }
}
