import firebase from 'firebase/app'

export default {
  actions: {
    async login ({ dispach, commit }, { email, password }) {
      try {
        await firebase.auth().signInWithEmailAndPassword(email, password)
      } catch (e) { }
    },
    async anonymLogin () {
      try {
        await firebase.auth().signInAnonymously()
      } catch (e) {}
    }
  }
}
