'use strict'

const molarMass = new Map([
  ['C', 12.0107],
  ['Cr', 51.9961],
  ['Mn', 54.938044],
  ['Mo', 95.96],
  ['Ni', 58.6934],
  ['Si', 28.0855],
  ['V', 50.9415],
  ['P', 30.973762],
  ['Fe', 55.845]
])

const R = 8.314

const diffusionDb = new Map([
  ['C', {
    D: 0.01,
    E: 200000
  }],
  ['Cr', {
    D: 0.0037,
    E: 267400
  }],
  ['Mo', {
    D: 0.00036,
    E: 240000
  }],
  ['V', {
    D: 0.0124,
    E: 274000
  }]
])

const getAlloyDensity = (db) => {
  return Object.keys(db)
    .map(key => {
      if (key !== 'composition') {
        return db[key].volume * db[key].density
      }
      return 0
    }).reduce((ac, val) => ac + val)
}

const getMolarVolume = (value) => {
  const c = value.composition
  if (c) {
    const molarVolume = {}
    Object.keys(c).forEach(el => (molarVolume[el] = c[el] / molarMass.get(el) * value.density * 1E6))
    return { ...value, composition: molarVolume }
  }
  return value
}

const convertComposition = (obj) => {
  const converted = getMolarVolume(obj)
  Object.keys(obj).map(key => {
    if (key !== 'composition') {
      converted[key] = getMolarVolume(obj[key])
    }
  })
  return converted
}

const diffusion = (temperature) => (el) => {
  const { D, E } = diffusionDb.has(el) ? diffusionDb.get(el) : 0
  return D * Math.exp(-E / (R * (temperature + 273.18)))
}

export default function strengthCalc ({ db }) {
  const list = { ...db.temperature['680'], density: getAlloyDensity(db.temperature['680']) }
  const convertedDb = convertComposition(list)
  const diffusionAtT = diffusion(680)
  const getCriticalTime = (carbide, radius) => {
    const criticalTime = {}
    const composition = convertedDb[carbide].composition
    const ferriteComposition = convertedDb.Ferrite.composition
    let flowControl = { criticalTime: 0 }
    Object.keys(composition).map(element => {
      const diffusion = diffusionAtT(element)
      if (diffusion) {
        criticalTime[element] = radius ** 2 / 2 / diffusion *
        (composition[element] - ferriteComposition[element]) /
        (composition[element] * convertedDb[carbide].volume)
      }
      if (flowControl.criticalTime < criticalTime[element]) {
        flowControl = {
          element,
          criticalTime: criticalTime[element]
        }
      }
    })
    if (flowControl.criticalTime) {
      convertedDb[carbide].flowControl = flowControl
      const element = flowControl.element
      convertedDb[carbide].rVst = (time) => Math.sqrt(2 * diffusionAtT(flowControl.element) *
        (composition[element] * convertedDb[carbide].volume) /
        (composition[element] - ferriteComposition[element]) *
        (time <= flowControl.criticalTime ? time : flowControl.criticalTime))
    }
    return criticalTime
  }
  // console.log(list)

  return (variables) => {
    for (const key in convertedDb) {
      if (key in variables) {
        convertedDb[key].particalVolume = convertedDb[key].volume / variables[key]
        convertedDb[key].radius = (convertedDb[key].particalVolume * 3 / 4 / Math.PI) ** (1 / 3)
        convertedDb[key].criticalTime = getCriticalTime(key, convertedDb[key].radius)
      }
    }
    // console.log('convertedDb: ', convertedDb)
    const dataset = []
    let maxTime = 0
    for (const carbide in convertedDb) {
      if (convertedDb[carbide].rVst) {
        if (maxTime < convertedDb[carbide].flowControl.criticalTime) {
          maxTime = convertedDb[carbide].flowControl.criticalTime
        }
      }
    }
    maxTime = Math.ceil((maxTime * 1.3) / 3600) + 1
    const time = []
    for (let i = 0; i < maxTime; ++i) {
      time.push(i)
    }
    for (const key in convertedDb) {
      if (convertedDb[key].rVst) {
        const radius = []
        const element = key
        for (const t of time) {
          radius.push(convertedDb[element].rVst(t * 3600) * 1e9)
        }
        dataset.push({
          element,
          radius
        })
      }
    }
    return { time, dataset }
  }
}
