<template>
  <div class="small">
    <line-chart
      v-if="datacollection"
      :chart-data="datacollection"
      :options="options"
      class="chart"
      :styles="myStyles"
    ></line-chart>
  </div>
</template>

<script>
import LineChart from '@/plugins/LineChart'

export default {
  components: {
    LineChart
  },
  data () {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [{
            gridLines: {
              display: false
            },
            scaleLabel: {
              display: true,
              labelString: 'Temperature, °C'
            }
          }],
          yAxes: [{
            gridLines: {
              color: 'rgb(226, 226, 226)'
            },
            scaleLabel: {
              display: true,
              labelString: 'Volume %'
            }
          }]
        }
      },
      myStyles: {
        height: '100%',
        width: '100%'
      }
    }
  },
  computed: {
    datacollection () {
      return this.fillData(this.$store.getters.current.db.temperature)
    }
  },
  methods: {
    fillData (value) {
      const added = []
      const lines = {
        labels: [],
        datasets: []
      }
      if (value) {
        Object.keys(value).forEach(entry => {
          lines.labels.push(+entry)
          Object.keys(value[entry]).forEach(el => {
            if (el !== 'composition') {
              if (added.indexOf(el) === -1) {
                added.push(el)
                const colorEgle = Math.floor(Math.random() * 360)
                lines.datasets.push({
                  label: el,
                  hidden: (el === 'Ferrite' || el === 'Austenite'),
                  backgroundColor: `hsla(${colorEgle}, 100%, 70%, .5)`,
                  borderColor: `hsl(${colorEgle}, 100%, 40%)`,
                  data: []
                })
              }
              lines.datasets.find(e => e.label === el).data.push(value[entry][el].volume * 100)
            }
          })
        })
      }
      return lines
    }
  }
}
</script>

<style lang="scss" scoped>
  .small {
    height: 50vh;
    margin-left: 30px;
    .chart{
      position: relative;
    }
  }
</style>
