<template>
  <div class="controls">
    <ul class="controls__list">
      <li class="controls__item">
        <label class="constols__label" for="baseM23C6">
          <span class="caption">Count</span>
          <span class="elementName">
            M<sub>23</sub>C<sub>6</sub>: {{ baseM23C6 | partisionalSize }}
          </span>
          <sup class="sup">{{ baseM23C6 | power }}</sup> m
          <sup>-3</sup>
        </label>
        <input
          type="range"
          class="range"
          id="baseM23C6"
          name="baseM23C6"
          min="1"
          max="20"
          step=".01"
          v-model.number.lazy="lazyCount.M23C6"
          v-model.number="baseM23C6"
        />
      </li>
      <li class="controls__item">
        <label class="constols__label" for="baseM7C3">
          <span class="caption">Count</span>
          <span class="elementName">
            M<sub>7</sub>C<sub>3</sub>: {{ baseM7C3 | partisionalSize }}
          </span>
          <sup class="sup">{{ baseM7C3 | power }}</sup> m
          <sup>-3</sup>
        </label>
        <input
          type="range"
          class="range"
          id="baseM7C3"
          name="baseM7C3"
          min="1"
          max="20"
          step=".01"
          v-model.number.lazy="lazyCount.M7C3"
          v-model.number="baseM7C3"
        />
      </li>
      <li class="controls__item">
        <label class="constols__label" for="baseMX">
          <span class="caption">Count</span>
          <span class="elementName">
            MX: {{ baseMX | partisionalSize }}</span>
          <sup class="sup">{{ baseMX | power }}</sup> m
          <sup>-3</sup>
        </label>
        <input
          type="range"
          class="range"
          id="baseMX"
          name="baseMX"
          min="50"
          max="200"
          step="1"
          v-model.number.lazy="lazyCount.MX"
          v-model.number="baseMX"
        />
      </li>
      <li class="controls__item">
        <label class="constols__label" for="baseMoC">
          <span class="caption">Count </span>
          <span class="elementName">
            MoC: {{ baseMoC | partisionalSize }}</span>
          <sup class="sup">{{ baseMoC | power }}</sup> m
          <sup>-3</sup>
        </label>
        <input
          type="range"
          class="range"
          id="baseMoC"
          name="baseMoC"
          min="50"
          max="200"
          step="1"
          v-model.number.lazy="lazyCount.MoC"
          v-model.number="baseMoC"
        />
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      baseM23C6: 2.5,
      baseM7C3: 2.5,
      baseMX: 100,
      baseMoC: 100,
      lazyCount: {
        M23C6: 0,
        M7C3: 0,
        MX: 0,
        MoC: 0
      }
    }
  },
  mounted () {
    this.lazyCount = { M23C6: 2.5, MX: 100, M7C3: 2.5, MoC: 100 }
  },
  computed: {
    lineColorizer: function (range) {
      const persent = Math.floor((100 * this.value) / range)
      return {
        background: `-webkit-linear-gradient(left ,#8870FF 0%, #8870FF ${persent}%, silver ${persent}%, silver 100%)`
      }
    }
  },
  watch: {
    lazyCount: {
      deep: true,
      handler () {
        this.$store.dispatch('setVariables', {
          M23C6: this.lazyCount.M23C6 * 10 ** 18,
          M7C3: this.lazyCount.M7C3 * 10 ** 18,
          MX: this.lazyCount.MX * 10 ** 18,
          MoC: this.lazyCount.MoC * 10 ** 18
        })
      }
    }
  },
  filters: {
    partisionalSize (num) {
      const tail = Math.floor(Math.log10(num))
      return (num / 10 ** tail).toFixed(2) + '·10'
    },
    power (num) {
      const tail = Math.floor(Math.log10(num))
      return (tail + 18).toString()
    }
  }
}
</script>
